export default ":host(.block) {\n    display: block;\n\n    width: 100%;\n    height: 100%;\n    box-sizing: border-box;\n\n    padding: 12px;\n\n    overflow-x: auto;\n\n    margin-bottom: 2px;\n}\n\n:host {\n\n    font-family: monospace;\n\n\n    /*border: 1px solid rgb(204, 204, 204);*/\n    border-radius: 3px;\n    background-color: light-dark(rgb(204, 204, 204), rgb(51, 51, 51) );\n\n    white-space: pre;\n    overflow-wrap: break-word;\n\n    /* ensures last line height even if empty */\n    &::after {\n        content: \"\\200b\"\n    }\n\n    & h {\n        white-space: nowrap;\n        background: light-dark(white, lightblue);\n        font-style: italic;\n        border: 1px dashed gray;\n        color: gray;\n        border-radius: 4px;\n\n        & var {\n            color: darkblue;\n            font-weight: bold;\n            font-style: normal;\n        }\n    }\n}";