export default ":host {\n    display: block;\n\n    & > div {\n\n        width: 100%;\n        height: 100%;\n        font-family: monospace;\n\n        box-sizing: border-box;\n        padding: 12px;\n\n        /*border: 1px solid rgb(204, 204, 204);*/\n        border-radius: 3px;\n        background-color: light-dark(rgb(204, 204, 204), rgb(51, 51, 51) );\n\n        white-space: pre;\n        overflow-wrap: break-word;\n\n        /* ensures last line height even if empty */\n        &::after {\n            content: \"\\200b\"\n        }\n    }\n}";