export default ":host {\n    display: grid;\n    grid : 1fr 1fr 1fr / 1fr 1fr 1fr;\n    gap: 5px;\n\n    justify-items: center;\n\n    width: 100%;\n\n    & .card {\n        width: 100%;\n        display: flex;\n        flex-flow: column nowrap; \n    }\n\n    & .card > .header {\n        text-align: center;\n    }\n    & .card > code-block {\n        width: 100%;\n        height: 100%;\n    }\n    & .card > iframe {\n        border: 1px solid black;\n        border-radius: 5px;\n        width: 100%;\n        height: fit-content;\n        box-sizing: border-box;\n    }\n}";